@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300&family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300&family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.textStyleTitle {
  font-family: 'Kanit', sans-serif;
  font-size: x-large;
}

.myColor {
  background-color: #292e31;
}

.mobileNav {
  display: none;
}

@media (min-width: 1000px) and (max-width: 3000px) {
  .myMobileNavCon {
    height: 0;
  }

  .mobileItem {
    display: none;
  }
}

@media (width: 768px) {
  .myNav {
    display: block;
  }
}

/*width for Mobile*/
@media (max-width: 768px) {
  .mobileTitle {
    width: 290px;
  }

  .mobileNav {
    display: inline-block;
  }

  .myMobileNavCon {
    top: 6.4%;
    height: 12%;
    display: none;
    z-index: 1;
    transition: 1s;
  }
}

@media (width: 768px) {
  .mobileTitle {
    width: 620px;
  }

  .myMobileNavCon {
    top: 5.1%;
    height: 9%;
    display: none;
    z-index: 1;
    transition: 1s;
  }
}

/*width for galaxy fold*/
@media (width: 280px) {
  .myMobileNavCon {
    top: 5.8%;
    height: 12%;
    display: none;
    z-index: 1;
    transition: 1s;
  }
}

/*width for surface duo*/
@media (width: 540px) {
  .mobileTitle {
    width: 450px;
  }
}

/*width for G-Pixel*/
@media (width: 411px) {
  .myMobileNavCon {
    top: 6.3%;
    height: 12%;
    display: none;
    z-index: 1;
    transition: 1s;
  }
}

@media (max-width: 360px) {
  .myFooter {
    width: 380px;
  }
}

.mobileSection {
  display: none;
}

@media (max-width: 480px) {
  .sectionZero {
    width: 360px;
    margin-left: -35px;
  }

  .sectionOne {
    width: 360px;
    margin-left: -35px;
  }

  .sectionTwo {
    margin-left: -37px;
    width: 360px;
  }

  .sectionThree {
    display: none;
  }

  .mobileSection {
    display: block;
    width: 360px;
    margin-left: -37px;
  }

  .mobileProvContainer {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 500px;
  }

  .mobileProvContainer::-webkit-scrollbar {
    display: none;
  }
}

@media (max-width: 480px) {
  .myInfoTitle {
    width: 350px;
    margin-left: -48px;
  }

  .infoContainer,
  .preventContainer {
    width: 350px;
    margin-left: -48px;
  }

  .infoImg {
    width: 80px;
    height: 80px;
  }
}

@media (min-width: 411px) and (max-width: 414px) {
  .myInfoTitle {
    width: 350px;
    margin-left: -30px;
  }

  .infoContainer,
  .preventContainer {
    width: 350px;
    margin-left: -30px;
  }
}

* {
  font-family: 'Poppins', sans-serif;
}

